import React from "react";
import { Route, Routes } from "react-router-dom";
import Cis from "./servicesPages/Cis";
// import Mep from "./servicesPages/Mep";
import Plugindev from "./servicesPages/Plugindev";
import Rendering from "./servicesPages/Rendering";
import ScanToBim from "./servicesPages/ScanToBim";
// import SteelStructure from "./servicesPages/SteelStructure";
import Precast from "./servicesPages/Precast";
import ServicesList from "./servicesPages/ServicesList";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
function Services() {
  return (
    <div>
      <Navbar />
      <Routes>
        <Route path="/serviceslist" element={<ServicesList />} />
        <Route path="/cis" element={<Cis />} />
        {/* <Route path="/mep" element={<Mep />} /> */}
        <Route path="/plugindevlopment" element={<Plugindev />} />
        <Route path="/precast" element={<Precast />} />
        <Route path="/rendering" element={<Rendering />} />
        <Route path="/scantobim" element={<ScanToBim />} />
        {/* <Route path="/steelstructure" element={<SteelStructure />} /> */}
      </Routes>
      <Footer />
    </div>
  );
}

export default Services;
