    heading: "DE Teams",
    content:
      "Detailed precast shop drawings using high end softwares like Allplan, AutoCAD, Revit and more. Potential to work with extreme quality and accuracy to help you reach your goals faster",
    img: "https://images.unsplash.com/photo-1519389950473-47ba0277781c?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80",
