import React from "react";
import Navbar from "../components/Navbar";
import Hero from "../components/Hero";
import Footer from "../components/Footer";
import { Card, Grid, Stack, Typography } from "@mui/material";
import { Container } from "@mui/system";
import { Formik } from "formik";
import * as Yup from "yup";
import TextFieldWrapper from "../components/formsUI/TextField";
import SubmitButton from "../components/formsUI/SubmitButton";
import emailjs from "emailjs-com";
const INITIAL_FORM_STATE = {
  firstName: "",
  lastName: "",
  email: "",
  message: "",
};

const FORM_VALIDATION = Yup.object().shape({
  firstName: Yup.string().required("Required"),
  lastName: Yup.string().required("Required"),
  email: Yup.string().email("Invalid email.").required("Required"),
  message: Yup.string().required("Required"),
});

function Contact() {
  function handleClick(values, { resetForm }) {
    emailjs
      .send(
        process.env.REACT_APP_SERVICE_ID,
        process.env.REACT_APP_TEMPLATE_ID,
        values,
        process.env.REACT_APP_USER_ID
      )
      .then(
        (result) => {
          resetForm({ values: "" });
        },
        (error) => {
          console.log(error.text);
        }
      );
    alert("Message sent successfully!");
  }
  return (
    <div>
      <Navbar />
      <Hero />
      <Card
        raised
        sx={{
          maxWidth: "75vw",
          height: "auto",
          padding: { sm: "50px", xs: "10px" },
          margin: "0 auto",
          transform: {
            lg: "translate(0, -15%)",
            xs: "translate(0,-5%)",
          },
          display: "flex",
          backgroundColor: "#fafafa",
          borderRadius: "15px",
        }}
      >
        <Container sx={{ textAlign: "center" }}>
          <Typography mb={5} variant="h4" fontWeight="bold">
            Contact Form
          </Typography>

          <Formik
            initialValues={{ ...INITIAL_FORM_STATE }}
            validationSchema={FORM_VALIDATION}
            onSubmit={handleClick}
          >
            {(formik) => (
              <form onSubmit={formik.handleSubmit}>
                <Grid mb={2} container spacing={3}>
                  <Grid item xs={12} sm={6}>
                    <TextFieldWrapper name="firstName" label="First Name" />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextFieldWrapper name="lastName" label="Last Name" />
                  </Grid>
                  <Grid item xs={12}>
                    <TextFieldWrapper name="email" label="email Id" />
                  </Grid>
                  <Grid item xs={12}>
                    <TextFieldWrapper
                      name="message"
                      label="Message"
                      multiline={true}
                      minrows={4}
                    />
                  </Grid>
                </Grid>
                <SubmitButton>
                  <Typography fontWeight="bold" color="white">
                    Send Query
                  </Typography>
                </SubmitButton>
              </form>
            )}
          </Formik>
        </Container>
      </Card>
      <Container>
        <Stack>
          <Typography variant="h6" fontWeight="700">
            Design Endeavour
          </Typography>
          <Typography>
            C-2/155, F/F, KH No. <br /> 264 New Ashok Nagar <br /> New Delhi,
            India
          </Typography>
          <Typography>
            <b>Ph No.: </b>+91 7011656582
          </Typography>
          <Typography>
            <b>Email: </b>info@designendeavour.com
          </Typography>
        </Stack>
      </Container>
      <Footer />
    </div>
  );
}

export default Contact;
