import { Button, Card, Typography } from "@mui/material";
import { Box, Stack } from "@mui/system";
import React, { useRef } from "react";
import stbimg from "../../images/serviceimages/stblaptop.png";
import SquareFootIcon from "@mui/icons-material/SquareFoot";
import EngineeringIcon from "@mui/icons-material/Engineering";
import ConstructionIcon from "@mui/icons-material/Construction";
import MapsHomeWorkIcon from "@mui/icons-material/MapsHomeWork";
import video from "../../images/video.mp4";
function ScanToBim() {
  const scollToRef = useRef();
  return (
    <div>
      <video
        style={{ width: "100%", objectFit: "cover", height: "50vh" }}
        src={video}
        autoPlay
        loop
        muted
      ></video>

      <Card
        raised
        sx={{
          maxWidth: "80vw",
          height: "100%",
          padding: " 10px 70px",
          margin: "0 auto",
          borderRadius: "15px",
          transform: {
            lg: "translate(0, -40%)",
            md: "translate(0,-35%)",
            sm: "translate(0,-20%)",
            xs: "translate(0,-16%)",
          },
          // bgcolor: "#f58125",
          bgcolor: "#323131",
          // backgroundImage:
          //   "linear-gradient(to bottom, rgba(255, 149,5,1), rgba(255, 149,5,0.85)) ",
          alignSelf: "center",
        }}
      >
        <Box mt={20}>
          <Stack direction={{ md: "row" }} alignItems="center" spacing={2}>
            <Stack flex={1}>
              <Typography variant="h4" fontWeight={700} color="primary">
                Scan to BIM - Unleash the potential of AEC industry
              </Typography>
              <Typography variant="h6" color="white">
                Expert surveyors and BIM modelers to provide you exceptional
                quality scanning and 3D detail ranging LOD 200 to as built
                models.
              </Typography>
            </Stack>
            <Stack flex={1} display={{ lg: "block" }}>
              <Box
                component="img"
                src={stbimg}
                alt="scan to bim"
                sx={{
                  position: { lg: "absolute" },
                  width: { lg: "600px", sm: "500px", xs: "300px" },
                  height: "auto",
                  transform: {
                    lg: "translate(12%,-40%)",
                    md: "translate(25%,0)",
                  },
                }}
              />
            </Stack>
          </Stack>
          <Stack direction="row" spacing={2} mt={5}>
            <Button
              variant="contained"
              sx={{
                bgcolor: "primary",
                color: "white",
                borderRadius: "20px",
              }}
            >
              <Typography variant="h7" fontWeight="700">
                Get Quote
              </Typography>
            </Button>
            <Button
              onClick={() =>
                scollToRef.current.scrollIntoView({ behavior: "smooth" })
              }
              variant="text"
              sx={{
                color: "white",
                borderRadius: "20px",
              }}
            >
              Know More
            </Button>
          </Stack>
          <Box mt={10} mb={2}>
            <Typography
              textAlign="center"
              variant="subtitle1"
              sx={{ color: "white" }}
            >
              Services available for
            </Typography>
            <Typography
              textAlign="center"
              sx={{ color: "white" }}
              variant="subtitle2"
            >
              Commercial || Industrial || Residential || Heritage Sites
            </Typography>
          </Box>
        </Box>
      </Card>
      <Box>
        <Stack
          direction={{ md: "row" }}
          alignItems="center"
          spacing={{ xs: 2, md: 2 }}
          padding={10}
          sx={{
            transform: { lg: "translate(0,-30%)", md: "translate(0,-20%)" },
          }}
        >
          <Stack
            flex={1}
            alignItems="center"
            justifyContent="center"
            textAlign="center"
            sx={{ color: "primary.dark" }}
            ref={scollToRef}
          >
            <MapsHomeWorkIcon fontSize="large" color="primary" />
            <Typography mt={3} mb={1} variant="h6" fontWeight={600}>
              Space Management
            </Typography>
            <Typography variant="subtitle1" sx={{ color: "grey" }}>
              With our Point Cloud to BIM services, you can take control of your
              space management. We enable you to view and enhance your workspace
              with more successful workflows.
            </Typography>
          </Stack>
          <Stack
            flex={1}
            alignItems="center"
            justifyContent="center"
            textAlign="center"
            sx={{ color: "primary.dark" }}
          >
            <ConstructionIcon fontSize="large" color="primary" />
            <Typography mt={3} mb={1} variant="h6" fontWeight={600}>
              Construction
            </Typography>
            <Typography variant="subtitle1" sx={{ color: "grey" }}>
              Take advantage of our point cloud to BIM conversion services in
              your construction project planning. Get accurate detailed plans
              and consultation to boost your ROI.
            </Typography>
          </Stack>
          <Stack
            flex={1}
            alignItems="center"
            justifyContent="center"
            textAlign="center"
            sx={{ color: "primary.dark" }}
          >
            <SquareFootIcon fontSize="large" color="primary" />
            <Typography mt={3} mb={1} variant="h6" fontWeight={600}>
              Documentation
            </Typography>
            <Typography variant="subtitle1" sx={{ color: "grey" }}>
              Our Point Cloud to BIM services streamline your documentation
              process. We provide precise and detailed 3D models, which saves
              you time and money.
            </Typography>
          </Stack>
          <Stack
            flex={1}
            alignItems="center"
            justifyContent="center"
            textAlign="center"
            sx={{ color: "primary.dark" }}
          >
            <EngineeringIcon fontSize="large" color="primary" />
            <Typography mt={3} mb={1} variant="h6" fontWeight={600}>
              Retrofitting
            </Typography>
            <Typography variant="subtitle1" sx={{ color: "grey" }}>
              Reliable surveying and scanning services to provide you with
              structured consultancy for adapting existing buildings, remodeling
              your space, and improving security.
            </Typography>
          </Stack>
        </Stack>
        {/* <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",
            height: "50vh",
            width: "100%",
            overflow: "hidden",
            backgroundImage: `linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.75))`,
            backgroundPosition: "center",
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
          }}
        >
          <Card
            raised
            sx={{
              maxWidth: "80vw",
              height: "80%",
              padding: " 10px 70px",
              margin: "0 auto",
              borderRadius: "15px",
              // transform: {
              //   lg: "translate(0, -40%)",
              //   md: "translate(0,-35%)",
              //   sm: "translate(0,-20%)",
              //   xs: "translate(0,-16%)",
              // },
              bgcolor: "#8A8A8A",
              // bgcolor: "#323131",
              // backgroundImage:
              //   "linear-gradient(to bottom, rgba(255, 149,5,1), rgba(255, 149,5,0.85)) ",
              alignSelf: "center",
            }}
          ></Card>
        </Box> */}
      </Box>
    </div>
  );
}

export default ScanToBim;
