import { IconButton } from "@mui/material";
import { Container, Stack } from "@mui/system";
import React from "react";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import FacebookIcon from "@mui/icons-material/Facebook";
import InstagramIcon from "@mui/icons-material/Instagram";
import { Link } from "react-router-dom";
function SocialGroup(props) {
  return (
    <Container>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="center"
        spacing={1}
        mt={2}
        mb={2}
      >
        <IconButton color={props.color} aria-label="LinkedIn Icon">
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to="https://www.linkedin.com/company/design-endeavour/"
          >
            <LinkedInIcon />
          </Link>
        </IconButton>
        <IconButton color={props.color} aria-label="Facebook Icon">
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to="https://www.facebook.com/designendeavour"
          >
            <FacebookIcon />
          </Link>
        </IconButton>
        <IconButton color={props.color} aria-label="Instagram Icon">
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to="https://instagram.com/designendeavour/"
          >
            <InstagramIcon />
          </Link>
        </IconButton>
      </Stack>
    </Container>
  );
}

export default SocialGroup;
