import { Container, Grid, Typography } from "@mui/material";
import { Stack } from "@mui/system";
import React from "react";
import CountUp from "react-countup";

function ContentGrid(props) {
  return (
    <Container maxWidth="70vw">
      <Grid container rowSpacing={7} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
        {props.data.map((data) => (
          <Grid key={data.id} item xs={12} md={6}>
            <Grid item>
              <Stack justifyContent="center" spacing={2} alignItems="center">
                <Typography
                  variant="h4"
                  textAlign="center"
                  fontWeight="600"
                  color="primary.dark"
                  maxWidth={{ md: "300px", xs: "200px" }}
                >
                  {data.gridCount ? (
                    <CountUp enableScrollSpy end={data.gridCount} />
                  ) : (
                    ""
                  )}
                  {data.gridHeading}
                </Typography>
                <Typography
                  maxWidth={{ md: "300px", xs: "200px" }}
                  textAlign="center"
                  variant="body"
                >
                  {data.gridBody}
                </Typography>
              </Stack>
            </Grid>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}

export default ContentGrid;
