import React from "react";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { Box, Card, Typography } from "@mui/material";
import SerProdCardGrid from "./servicesPages/SerProdCardGrid";
import bimf from "../images/blogs/bimfuture.jpg";
import bimcons from "../images/blogs/bimconstruction.jpg";
import bimchal from "../images/blogs/bimchallenge.jpg";
import stb from "../images/blogs/stb.jpg";
import precast from "../images/blogs/precast.jpg";
import render from "../images/blogs/render.jpg";
function Blog() {
  return (
    <div>
      <Navbar />
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          height: "50vh",
          width: "100%",
          overflow: "hidden",
          backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.3)), url(https://images.unsplash.com/photo-1481627834876-b7833e8f5570?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=628&q=80)`,
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
        }}
      >
        <Typography
          variant="h4"
          fontWeight={600}
          color="white"
          textAlign="center"
        >
          Articles and Case Studies
        </Typography>
        <Typography color="primary">Get ahead with our research</Typography>
      </Box>
      <Card
        raised
        sx={{
          maxWidth: "95vw",
          height: "100%",
          padding: " 90px 20px",
          margin: "0 auto",
          borderRadius: "15px",
          transform: {
            lg: "translate(0, -9%)",
            sm: "translate(0,-3%)",
            xs: "translate(0,-2%)",
          },
          display: "flex",
          justifyContent: "center",
        }}
      >
        <SerProdCardGrid data={data} />
      </Card>
      <Footer />
    </div>
  );
}
const data = [
  {
    id: 1,
    heading: "The future of BIM and its potential impact on the AEC industry",
    content:
      "BIM technology is expected to become even more important to the AEC sector and have a big impact on construction.",
    img: bimf,
    link: "https://www.linkedin.com/posts/design-endeavour_bim-allplan-architecture-activity-7030785549514813440-HePl?utm_source=share&utm_medium=member_desktop",
  },
  {
    id: 2,
    heading:
      "The Use of BIM in Construction Project Management and Collaboration",
    content:
      "BIM is a potent technology that can enhance communication and coordination amongst various project stakeholders.",
    img: bimcons,
    link: "https://www.linkedin.com/feed/update/urn:li:activity:7028610003972329472",
  },
  {
    id: 3,
    heading:
      "The challenges and limitations of BIM adoption in the AEC industry",
    content:
      "The AEC may undergo a revolution thanks to the powerful tool known as BIM. However, BIM adoption has its own set of difficulties and restrictions, much like the use of any new technology.",
    img: bimchal,
    link: "https://www.linkedin.com/feed/update/urn:li:activity:7023533522921373696",
  },
  {
    id: 4,
    heading:
      "Designing the future: the power of 3DS Max in architecture and interior design",
    content:
      "With the help of the potent architecture rendering program Autodesk 3DS Max, designers can produce incredibly accurate and in-depth renderings of their work",
    img: render,
    link: "https://www.linkedin.com/feed/update/urn:li:activity:7022115331875704832",
  },
  {
    id: 5,
    heading: "Precast Concrete: The Future of Construction in India.",
    content:
      "Precast concrete is a type of construction that involves casting the concrete, allowing it to cure in a controlled setting, like a factory, and then transporting it to the job site to be installed.",
    img: precast,
    link: "https://www.linkedin.com/feed/update/urn:li:activity:7019551479409586177",
  },
  {
    id: 6,
    heading:
      "Understanding the Process of Converting Point Clouds to 3D Models",
    content:
      "Converting point clouds to CAD data and subsequently 3D models is one of the most crucial features of BIM. Scan to BIM conversion is the term used for this procedure.",
    img: stb,
    link: "https://www.linkedin.com/feed/update/urn:li:activity:7021425632811073536",
  },
];

export default Blog;
