import { Button, Grid, Paper, Stack, Typography } from "@mui/material";
import { Container } from "@mui/system";
import React from "react";
import { Link } from "react-router-dom";

function SerProdCardGrid(props) {
  return (
    <Container>
      <Grid container rowSpacing={7} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
        {props.data.map((data) => (
          <Grid item xs={12} sm={6} md={4} key={data.id}>
            <Paper
              sx={{
                maxWidth: 345,
                maxHeight: 500,
                padding: 2,
                WebkitBoxShadow: "  -5px 5px 13px -10px rgba(0,0,0,0.75)",
                boxShadow: " -5px 5px 13px -10px rgba(0,0,0,0.75)",
                bgcolor: "#fafafa",
              }}
            >
              <img
                src={data.img}
                alt="service"
                style={{
                  width: "100%",
                  height: "200px",
                  borderRadius: "7px",
                  transform: "translate(0, -15%)",
                  WebkitBoxShadow: "  -6px 23px 50px -31px rgba(0,0,0,0.75)",
                  boxShadow: " -6px 23px 50px -31px rgba(0,0,0,0.75)",
                }}
              />

              <Stack minHeight={150} paddingLeft={1}>
                <Typography gutterBottom variant="h5" component="div">
                  {data.heading}
                </Typography>
                <Typography variant="body2" mb={1} color="text.secondary">
                  {data.content}
                </Typography>
              </Stack>
              <Button size="small" color="primary">
                <Link
                  to={data.link}
                  style={{
                    textDecoration: "none",
                    color: "inherit",
                    fontWeight: 600,
                  }}
                >
                  learn more
                </Link>
              </Button>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}

export default SerProdCardGrid;
