import { Typography } from "@mui/material";
import { Box, Container, Stack } from "@mui/system";
import React from "react";
import ContentGrid from "./ContentGrid";
const data = [
  {
    id: 1,
    gridCount: 100,
    gridHeading: "+",
    gridBody:
      "Projects completed by our experts, delivering 100% client satisfaction",
    gridIcon: "something here",
  },
  {
    id: 2,
    gridCount: 10,
    gridHeading: "+",
    gridBody: "years of building design and detailing experience.",
    gridIcon: "something here",
  },
  {
    id: 3,
    gridCount: 100,
    gridHeading: "+",
    gridBody:
      "skilled in multistory building design, such as shop drawings, architecture drawings and BIM coordination",
    gridIcon: "something here",
  },
  {
    id: 4,
    gridCount: 100,
    gridHeading: "%",
    gridBody:
      "commitment to the high accuracy and quality of the design process. Performing 100% delivery rate",
    gridIcon: "something here",
  },
];
function ContentContainer(props) {
  return (
    <Container>
      <Box>
        <Stack spacing={2}>
          <Typography
            variant="h3"
            color="primary.dark"
            sx={{ fontWeight: "700" }}
          >
            {props.mainHeading}
          </Typography>
          <Typography variant="h4">{props.secHeading}</Typography>
          <Typography variant="h5" color="primary" fontWeight="700">
            {props.subHeading}
          </Typography>
          <Typography variant="body1">{props.body}</Typography>
        </Stack>
      </Box>
      <Box mt={12}>
        <ContentGrid data={data} />
      </Box>
    </Container>
  );
}

export default ContentContainer;
