import { List, ListItem, Typography } from "@mui/material";
import { Box, Stack } from "@mui/system";
import SocialGroup from "./SocialGroup";
import "react-phone-number-input/style.css";
import logo from "../images/Logo5.png";
import { Link } from "react-router-dom";
const pages = [
  {
    id: 1,
    page: "Home",
    Link: "/",
  },
  {
    id: 2,
    page: "About",
    Link: "/about",
  },
  {
    id: 3,
    page: "Contact",
    Link: "/contact",
  },
  {
    id: 4,
    page: "Blog",
    Link: "/blog",
  },
  {
    id: 5,
    page: "Learning",
    Link: "/learning",
  },
  {
    id: 6,
    page: "Services",
    Link: "/services/serviceslist",
  },
  // {
  //   id: 7,
  //   page: "Products",
  //   Link: "/products/productlist",
  // },
];
const services = [
  {
    id: 1,
    service: "Scan to BIM",
    link: "/services/scantobim",
  },

  {
    id: 2,
    service: "Precast RCC Structure",
    link: "/services/precast",
  },

  {
    id: 3,
    service: "Cast in Situ",
    link: "/services/cis",
  },

  // {
  //   id: 4,
  //   service: "Steel Structure",
  //   link: "/services/steelstructure",
  // },

  // {
  //   id: 5,
  //   service: "MEP Services",
  //   link: "/services/mep",
  // },

  {
    id: 4,
    service: "3D Rendering",
    link: "/services/rendering",
  },

  {
    id: 5,
    service: "Plugin Development",
    link: "/services/plugindevlopment",
  },
];
const legals = [
  { id: 1, legal: "Privacy Policies", link: "/privacy" },
  {
    id: 2,
    legal: "Terms and Conditions",
    link: "/termsandconditions",
  },
  {
    id: 3,
    legal: "DE Teams",
    link: "/products/deteams",
  },
];
function Footer() {
  const currentYear = new Date().getFullYear();
  return (
    <Box
      width="100%"
      mt={20}
      sx={{
        backgroundImage: `linear-gradient(to top, rgba(0, 0, 0, 0.95), rgba(0, 0, 0, 0.85))`,
      }}
    >
      <Stack
        padding={1}
        direction={{ md: "row" }}
        justifyContent="center"
        alignItems="center"
        spacing={{ xs: 2, sm: 3, lg: 10 }}
        sx={{ color: "#E5E5E5" }}
      >
        <Stack>
          <Stack direction="row" spacing={3}>
            <Stack alignItems="center" justifyContent="center">
              <img
                src={logo}
                style={{
                  marginTop: "20px",
                  height: "50px",
                  width: "auto",
                }}
                alt="Design Enedavour Logo"
              />
              <SocialGroup color="primary" />
            </Stack>
            <List>
              {legals.map((legal) => (
                <ListItem key={legal.id}>
                  <Link
                    style={{ textDecoration: "none", color: "inherit" }}
                    to={legal.link}
                  >
                    {legal.legal}
                  </Link>
                </ListItem>
              ))}
            </List>
          </Stack>
        </Stack>
        <Stack>
          <Stack direction="row" spacing={1}>
            <List>
              {services.map((service) => (
                <ListItem key={service.id}>
                  <Link
                    style={{ textDecoration: "none", color: "inherit" }}
                    to={service.link}
                  >
                    {service.service}
                  </Link>
                </ListItem>
              ))}
            </List>
            {/* here------------ */}
            <List disablePadding>
              {pages.map((page) => (
                <ListItem key={page.id}>
                  <Link
                    style={{ textDecoration: "none", color: "inherit" }}
                    to={page.Link}
                  >
                    {page.page}
                  </Link>
                </ListItem>
              ))}
            </List>
            {/* Here------------------- */}
          </Stack>
        </Stack>
        <Stack justifyContent="center" alignItems="center" spacing={3}>
          {/* <PhoneInput
            placeholder="Enter phone number"
            value={value}
            onChange={setValue}
          /> */}

          {/* <Button
            size="small"
            variant="contained"
            sx={{ width: "200px" }}
            color="inherit"
          >
            <Typography color="black">Book Call</Typography>
          </Button> */}
          <Typography variant="body">
            C-2/155, F/F, KH No. 264 New Ashok Nagar
            <br />
            New Delhi, India
            <br />
            110096
            <br />
            Ph No. :+91 7011656582
            <br />
            <br />
            All Rights Reserved,  {currentYear} Design Endeavours
          </Typography>
        </Stack>
      </Stack>
    </Box>
  );
}

export default Footer;
