import * as React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { Link } from "react-router-dom";

export default function BasicCard(props) {
  return (
    <div style={{ padding: "5px" }}>
      <Card
        raised
        sx={{
          minWidth: { md: "25vw", sm: "350px" },
          backgroundColor: `${props.bcolor}`,
        }}
      >
        <CardContent>
          <Typography variant="h5" component="div" color={props.tcolor}>
            {props.mh}
          </Typography>
          <Typography sx={{ mb: 1.5 }} color={props.tcolor}>
            {props.sh}
          </Typography>
          <Typography
            variant="body2"
            minWidth="300px"
            maxWidth="500px"
            color={props.tcolor}
          >
            {props.body}
          </Typography>
        </CardContent>
        <CardActions>
          <Link
            to={props.link}
            style={{
              textDecoration: "none",
              color: "inherit",
              textTransform: "capitalize",
            }}
          >
            <Button variant={props.btnvar}>
              <Typography
                color={props.tcolor}
                sx={{ textTransform: "capitalize" }}
              >
                {props.btnText}
              </Typography>
            </Button>
          </Link>
        </CardActions>
      </Card>
    </div>
  );
}
