import React from "react";
import Navbar from "../components/Navbar";
import Hero from "../components/Hero";
import Footer from "../components/Footer";
import { Button, Card, Container, Typography } from "@mui/material";
import { Stack } from "@mui/system";
import boximage from "../images/website/website images/aboutimg.jpg";
import ContentGrid from "../components/ContentGrid";
import SocialGroup from "../components/SocialGroup";
const data = [
  {
    id: 1,

    gridHeading: "Our Mission",
    gridBody:
      "Our mission is to provide the rapid and high-detail designs with perfection that will prove to be the most valuable asset to our clients",
  },
  {
    id: 2,

    gridHeading: "Our Vision",
    gridBody:
      "We aim to create an end-to-end design process that can create a platform for the builders, engineers and architects to have everything, all at once, in just one place.",
  },
  {
    id: 3,

    gridHeading: "Trust",
    gridBody:
      "Our company believes in developing long-term relationships with our clients. Assuring them of the benefits of our services.",
  },
  {
    id: 4,

    gridHeading: "Efficiency",
    gridBody:
      "Our diligent and committed team ensures to provide optimized solutions for all the client's needs.",
  },
  {
    id: 5,

    gridHeading: "Reliability",
    gridBody:
      "Our team has experience of more than 10 years, providing our clients with a reliable and trustworthy experience.",
  },
  {
    id: 6,

    gridHeading: "Commitment",
    gridBody:
      "Our team is dedicated and committed to generating the most value for our clients by becoming their most valuable asset in their business.",
  },
];
function About() {
  return (
    <div>
      <Navbar />
      <Hero />
      <Card
        raised
        sx={{
          maxWidth: "75vw",
          height: "auto",
          padding: { sm: "50px", xs: "10px" },
          margin: "0 auto",
          transform: {
            lg: "translate(0, -15%)",
            xs: "translate(0,-5%)",
          },
          display: "flex",
          backgroundColor: "#fcfcfc",
          borderRadius: "15px",
        }}
      >
        <Stack spacing={1} pb={20}>
          <Stack
            mb={10}
            justifyContent="space-between"
            alignItems="center"
            direction={{ sm: "row" }}
            spacing={{ xs: 3 }}
          >
            <Typography variant="h3" fontWeight={600} color="#534D4D">
              About Us
            </Typography>
            <Button
              variant="contained"
              sx={{ borderRadius: "45px", padding: "10px 35px 10px 35px" }}
            >
              <Typography color="white" fontWeight={600}>
                Our Team
              </Typography>
            </Button>
          </Stack>
          <Typography>
            <b>Design Endeavour</b> is a leading provider of BIM modeling{" "}
            <b>services to the AEC industry.</b> Founded in 2022, our mission is
            to help architects, engineers, and contractors design, build, and
            maintain buildings with greater precision, efficiency, and safety.
            <Typography>
              <br />
              We specialize in using cutting-edge software like{" "}
              <b> AutoCAD, Allplan, Revit, Tekla, 3DS MAX</b> to create detailed
              3D models of buildings that can be used for planning, design,
              construction, and operations. Our services include{" "}
              <b>
                scan to BIM, architectural modeling, precast shop drawings,
                structural engineering, MEP engineering, cost estimation, and
                building analysis
              </b>
            </Typography>
            <Typography>
              <br />
              Our team is composed of skilled professionals with decades of
              experience in the AEC industry. We work closely with our clients
              to understand their unique needs and{" "}
              <b>
                deliver customized solutions that meet their specific
                requirements.
              </b>
            </Typography>
            <Typography>
              <br />
              We have done a <b> variety of projects</b> for a variety of
              clients. We consult with them based on their project requirements,
              time frame, and budget. Our aim is to address our clients
              objectives as if they were our own, thus we work with pinpoint
              accuracy to find the best approach to provide our clients an{" "}
              <b>advantage in a competitive market.</b>
            </Typography>
            <br />
            If you're interested in learning more about our BIM modeling
            services, we would love to hear from you. Please don't hesitate to
            contact us to discuss your project in more detail. Feel free to
            email us at <b> www.info@designendeavour.com</b>.
          </Typography>
        </Stack>
      </Card>
      <Card
        raised
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          width: "100%",
          height: "300px",
          backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.55), rgba(0, 0, 0, 0.55)), url(${boximage})`,
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
          backgroundAttachment: "fixed",
          transform: {
            lg: "translate(0, -55%)",
            xs: "translate(0,-35%)",
          },
        }}
      >
        <Typography
          textAlign="center"
          variant="h3"
          letterSpacing={5}
          fontWeight={800}
          color="primary"
        >
          Our Philosophy
        </Typography>
      </Card>
      <Card
        raised
        sx={{
          maxWidth: "75vw",
          height: "auto",
          padding: { sm: "50px", xs: "10px" },
          margin: "0 auto",
          transform: {
            lg: "translate(0, -25%)",
            md: "translate(0,-17%)",
            sm: "translate(0,-10%)",
            xs: "translate(0,-9%)",
          },
          display: "flex",
          backgroundColor: "#fcfcfc",
          borderRadius: "15px",
        }}
      >
        <Stack textAlign="center" mt={10} mb={10}>
          <ContentGrid data={data} />
        </Stack>
      </Card>
      <Container>
        <Typography textAlign="center">Find us on Social Media</Typography>
        <SocialGroup />
      </Container>
      <Footer />
    </div>
  );
}

export default About;
