import React from "react";
import { Route, Routes } from "react-router-dom";
import AcadSolutions from "./productPages/AcadSolutions";
import RevitSolutions from "./productPages/RevitSolutions";
import AutomationTools from "./productPages/AutomationTools";
import DeTeams from "./productPages/DeTeams";
import ProductList from "./productPages/ProductList";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
function Products() {
  return (
    <div>
      <Navbar />

      <Routes>
        <Route path="/autocadsolutions" element={<AcadSolutions />} />
        <Route path="/revitsolutions" element={<RevitSolutions />} />
        <Route path="/automationtools" element={<AutomationTools />} />
        <Route path="/deteams" element={<DeTeams />} />
        <Route path="/productlist" element={<ProductList />} />
      </Routes>
      <Footer />
    </div>
  );
}

export default Products;
