import React from "react";
import Navbar from "../components/Navbar";
import Hero from "../components/Hero";
import { Box, Button, Card, Stack, Typography } from "@mui/material";

function Privacy() {
  return (
    <div>
      <Navbar />
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          height: "30vh",
          width: "100%",
          overflow: "hidden",
          backgroundImage: `linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.75))`,
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
        }}
      ></Box>
      <Card
        raised
        sx={{
          maxWidth: "75vw",
          height: "auto",
          padding: { sm: "50px", xs: "10px" },
          margin: "0 auto",
          transform: {
            lg: "translate(0, -5%)",
            xs: "translate(0,-1%)",
          },
          display: "flex",
          backgroundColor: "#fcfcfc",
          borderRadius: "15px",
        }}
      >
        <Stack spacing={1} pb={20}>
          <Stack
            mb={10}
            justifyContent="space-between"
            alignItems="center"
            direction={{ sm: "row" }}
            spacing={{ xs: 3 }}
          >
            <Typography variant="h3" fontWeight={600} color="#534D4D">
              Privacy Policy
            </Typography>
          </Stack>
          <Typography>
            This privacy policy (the "Policy") applies to Design Endeavour, a
            company that provides architecture, engineering, and construction
            services, including designing and building information modelling
            (BIM). This Policy explains how we collect, use, and share personal
            information about you when you visit our website or use our
            services. By accessing or using our website or services, you
            acknowledge that you have read, understood, and agree to the
            collection, use, and sharing of your personal information as
            described in this Policy. If you do not agree with our policies and
            practices, do not use our website or services.
            <br />
            <br />
            <b> COLLECTION OF PERSONAL INFORMATION </b>
            <br />
            <br />
            We may collect personal information about you in a variety of ways,
            including when you:
            <ul>
              <li>Visit our website</li>
              <li>Request information or services from us</li>
              <li>
                Provide information to us through our website or other online
                platforms
              </li>
              <li>Communicate with us through phone, email, or other means</li>
              <li>
                The personal information we collect may include your name, email
                address, phone number, and any other information you choose to
                provide to us.
              </li>
            </ul>
            <br />
            <b>USE OF PERSONAL INFORMATION</b>
            <br />
            <br />
            We may use your personal information for a variety of purposes,
            including to:
            <ul>
              <li>Respond to your requests for information or services</li>
              <li>Communicate with you about your project or request</li>
              <li>
                Provide you with updates or information about our company or
                services
              </li>
              <li>Analyse and improve our website and services</li>
            </ul>
            <br />
            <b>SHARING OF PERSONAL INFORMATION</b>
            <br />
            <br />
            We may share your personal information with third parties in the
            following circumstances:
            <ul style={{ listStyle: "none" }}>
              <li>
                <b> With your consent: </b>We may share your personal
                information with third parties if you have given us your consent
                to do so.
              </li>
              <li>
                <b>With service providers: </b>We may share your personal
                information with service providers who perform services on our
                behalf, such as website hosting, data analysis, payment
                processing, and marketing. These service providers are required
                to protect your personal information and may not use it for any
                other purpose.
              </li>
              <li>
                <b>As required by law:</b> We may be required to disclose your
                personal information in response to a subpoena, court order, or
                other legal process.
              </li>
            </ul>
            <br />
            <b>SECURITY OF PERSONAL INFORMATION</b>
            <br />
            <br />
            We take reasonable measures to protect your personal information
            from loss, misuse, and unauthorised access, disclosure, alteration,
            or destruction. However, no internet or email transmission is ever
            fully secure or error-free, so you should take special care in
            deciding what information you send to us.
            <br />
            <br />
            <b>ACCESS TO AND CONTROL OVER PERSONAL INFORMATION</b>
            <br />
            <br />
            You have the right to access and control your personal information.
            You can request access to your personal information by contacting us
            using the contact information provided below. You can also ask us to
            update or correct any inaccuracies in your personal information.
            <br />
            <br />
            <b>CHANGES TO THIS POLICY</b>
            <br />
            <br />
            We may update this Policy from time to time to reflect changes in
            our practices or laws. We encourage you to review this Policy
            periodically for the latest information on our privacy practices.
            <br />
            <br />
            <b>CONTACT US</b>
            <br />
            <br />
            If you have any questions about this Policy or our privacy
            practices, please contact us at:
            <br />
            <br />
            <b>Design Endeavour</b>
            <br />
            C-2/155, F/F, KH No. 264 New Ashok Nagar
            <br />
            New Delhi, India
            <br />
            110096
            <br />
            <b>Email: </b>info@designendeavour.com
            <br />
            <b>phone:</b> +91 7011656582
          </Typography>
        </Stack>
      </Card>
    </div>
  );
}

export default Privacy;
