import { Box, Card, Typography } from "@mui/material";
import { Stack } from "@mui/system";
import React from "react";
import SocialGroup from "./SocialGroup";

function Developing() {
  return (
    <div>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          height: "50vh",
          width: "100%",
          overflow: "hidden",
          backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.3)), url(https://images.unsplash.com/photo-1522071820081-009f0129c71c?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80)`,
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
        }}
      ></Box>
      <Card
        raised
        sx={{
          maxWidth: "95vw",
          height: "100%",
          padding: " 90px 20px",
          margin: "0 auto",
          borderRadius: "15px",
          transform: {
            lg: "translate(0, -7%)",
            sm: "translate(0,-3%)",
            xs: "translate(0,-2%)",
          },
          display: "flex",
          justifyContent: "center",
        }}
      >
        <Stack>
          <Typography variant="h4">
            Currently in Development. Stay tuned for more information
          </Typography>
          <SocialGroup />
        </Stack>
      </Card>
    </div>
  );
}

export default Developing;
