import { Container, Stack } from "@mui/system";
import React from "react";
import autocad from "../images/softwarelogo/autocad_logo.png";
import revit from "../images/softwarelogo/revit_logo.png";
import allplan from "../images/softwarelogo/allplan_logo.png";
// import tekla from "../images/softwarelogo/Tekla_Structures_Logo.png";
import max from "../images/softwarelogo/3dsMax_logo.png";
import recap from "../images/softwarelogo/recap_logo.png";
import { Typography } from "@mui/material";
function LogoBox() {
  return (
    <Stack
      width="100%"
      height="auto"
      padding={{ md: "50px", xs: "10px" }}
      sx={{ backgroundColor: "rgba(200,200,200,0.3)" }}
    >
      <Typography
        variant="h5"
        color="primary.dark"
        textAlign="center"
        fontWeight="600"
        mb="25px"
      >
        Skills on all kinds of <b style={{ color: "#f58125" }}>BIM</b> tools
      </Typography>
      <Container>
        <Stack
          direction={{ xs: "column", md: "row" }}
          justifyContent="center"
          alignItems="center"
          spacing={{ xs: 3, md: 5 }}
        >
          <img
            src={allplan}
            alt={"logo of allplan"}
            height="40px"
            width="140px"
          />
          <img
            src={revit}
            alt={"logo of allplan"}
            height="40px"
            width="130px"
          />
          <img
            src={autocad}
            alt={"logo of allplan"}
            height="40px"
            width="120px"
          />
          <img src={max} alt={"logo of allplan"} height="50" width="120px" />
          <img
            src={recap}
            alt={"logo of allplan"}
            height="40px"
            width="160px"
          />
        </Stack>
      </Container>
    </Stack>
  );
}

export default LogoBox;
